.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 12vh;
  background: linear-gradient(rgba(0, 0, 0, 1), rgba(26, 10, 10, 0.8));
  border-bottom: 2px solid rgb(236, 15, 15);
}
.nav__brand {
  text-transform: uppercase;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-gap: 3rem;
  gap: 3rem;
}
.nav__toggler {
  display: none;
}
.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  background: rgb(204, 204, 204);
  transition: 0.3s ease-in;
}

@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
  }
  .nav__menu {
    position: fixed;
    top: 7vh;
    right: 0;
    height: 93vh;
    width: 50vw;
    background: linear-gradient(rgba(0, 0, 0, 1), rgba(26, 10, 10, 0.8));
    border-left: 2px solid rgb(236, 15, 15);
    flex-direction: column;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.5s ease-in;
  }
}
/* Active Class */
.nav__active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

/* Toggle Icon Animation */

.toggle .line1 {
  -webkit-transform: rotate(-45deg) translate(-4px, 5px);
          transform: rotate(-45deg) translate(-4px, 5px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  -webkit-transform: rotate(45deg) translate(-4px, -5px);
          transform: rotate(45deg) translate(-4px, -5px);
}

